  text-align: center;
  border: 0;
}
div.kukai-modal.top {
  z-index: 101;
}
div.kukai-modal > div.km-content {
  position: relative;
  background-color: #fcfcfc;
  margin: 5rem auto;
  border: 1px solid #ffffff;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.04), 0px 2px 6px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.06);
  border-radius: 0.75rem;
  width: 25rem;
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.5s;
  animation-name: animatetop;
  animation-duration: 0.5s;
}
div.kukai-modal > div.km-content > div.km-header {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  border-bottom: 1px solid black;
  border-bottom: 1px solid rgba(0, 0, 0, 0.04);
}
div.kukai-modal.confirm > div.km-content {
  -webkit-animation-name: none;
  -webkit-animation-duration: none;
  animation-name: none;
  animation-duration: none;
}

div.km-header > h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 1.25rem;
  line-height: 1.5rem;
  display: flex;
  align-items: center;
  color: #2c323a;
  margin: 1rem;
}
div.km-header > span.close {
  margin: 0.25rem 0.5rem;
  color: #aaa;
  font-size: 28px;
  font-weight: bold;
}
div.km-header > span.close:hover, div.km-header > span.close:focus {
  color: #2c323a;
  text-decoration: none;
  cursor: pointer;
}

div.km-body {
  margin: 1rem;
}

div.km-content label:not(.switch) {
  margin-bottom: 0.5rem;
  font-style: normal;
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.125rem;
  display: flex;
  align-items: center;
  color: #2C323A;
  opacity: 0.4;
}

div.kukai-modal p,
span.danger {
  font-style: normal;
  font-weight: normal;
  font-size: 0.875rem;
  line-height: 1rem;
  display: flex;
  align-items: flex-end;
  color: #000000;
}

@media screen and (max-width: 27rem) {
  div.kukai-modal div.x {
    right: -1px;
    border-bottom-right-radius: 0;
  }

  div.kukai-modal button.small.multiple-destinations {
    margin-right: 2.5rem;
  }
}
@media screen and (max-width: 25rem) {
  div.kukai-modal > div.km-content {
    width: 23rem;
  }

  div.kukai-modal input.text.password {
    width: 11rem;
  }
}
.switch {
  position: relative;
  display: inline-block;
  width: 3rem;
  height: 1.5rem;
  margin-bottom: 0;
}
.switch input {
  opacity: 0;
  -webkit-opacity: 0;
  -khtml-opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #d9d9d9;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.slider:before {
  position: absolute;
  content: "";
  height: 1.125rem;
  width: 1.125rem;
  left: 0.1875rem;
  bottom: 0.1875rem;
  background-color: #7B7B7B;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.slider.round {
  border-radius: 2.125rem;
}
.slider.round:before {
  border-radius: 50%;
}

input:checked + .slider {
  box-shadow: 0px 0px 1px rgba(89, 99, 255, 0.04), 0px 2px 6px rgba(89, 99, 255, 0.04), 0px 16px 24px rgba(89, 99, 255, 0.24);
}
input:checked + .slider:before {
  background-color: #5963FF;
  -webkit-transform: translateX(1.5rem);
  -ms-transform: translateX(1.5rem);
  transform: translateX(1.5rem);
}

input:checked + .slider {
  background-color: rgba(89, 99, 255, 0.4);
}

div.kukai-card > div.grey-card > div.switch-container > span.switch-desc,
span.switch-desc {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.16px;
  color: #161616;
  margin-left: 0.5rem;
}

div.switch-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  width: 28rem;
}

small {
  font-style: normal;
  font-weight: normal;
  font-size: 0.75rem;
  line-height: 1rem;
  letter-spacing: 0.32px;
  color: #393939;
  margin-top: 2.5rem;
  margin-bottom: 0.5rem;
}

@media screen and (max-width: 29rem) {
  div.switch-container {
    width: 20rem;
  }
}